import React, { useCallback, useState } from 'react';
import ScrollMenu from 'react-horizontal-scrolling-menu';
import Gallery from "react-photo-gallery";
import Carousel, { Modal, ModalGateway } from "react-images";
import { Timeline, TimelineItem } from 'vertical-timeline-component-for-react';

import {
    technologies,
    projects,
    photos,
    experiences
} from './data';

import { CardProgress, Profile } from './components';

const Arrow = (props) => {
    return (
        <div
            className={props.className}
        >{props.text}</div>
    );
};

function App() {

    const [currentImage, setCurrentImage] = useState(0);
    const [viewerIsOpen, setViewerIsOpen] = useState(false);

    const openLightbox = useCallback((event, { photo, index }) => {
        setCurrentImage(index);
        setViewerIsOpen(true);
    }, []);

    const closeLightbox = () => {
        setCurrentImage(0);
        setViewerIsOpen(false);
    };

    const ArrowLeft = Arrow({ text: '<', className: 'arrow' });
    const ArrowRight = Arrow({ text: '>', className: 'arrow' });

    return (
        <div>
            <div className="box-profile">
                <Profile />
                <h3>EXPERIÊNCIA PROFISSIONAL</h3>
            </div>
            <Timeline lineColor={'#1b1b1b'}>
                {experiences.map((experience, index) => {
                    return (
                        <TimelineItem
                            key={index}
                            dateText={experience.company}
                            style={{ color: '#e86971' }}
                        >
                            <h3 style={{ color: '#FFF' }}>{experience.title}</h3>
                            <h5 style={{ color: '#FFF' }}>{experience.sub_title}</h5>
                            <br />
                            {experience.description.map(description => (
                                <p style={{ color: '#FFF' }}>{description}</p>
                            ))}
                        </TimelineItem>
                    );
                })}
            </Timeline>
            <Gallery photos={projects} onClick={openLightbox} />
            <ModalGateway>
                {viewerIsOpen ? (
                    <Modal onClose={closeLightbox}>
                        <Carousel
                            currentIndex={0}
                            views={photos[currentImage].map(x => ({
                                ...x,
                                srcset: x.srcSet,
                                caption: x.title
                            }))}
                        />
                    </Modal>
                ) : null}
            </ModalGateway>
            <ScrollMenu
                arrowLeft={ArrowLeft}
                arrowRight={ArrowRight}
                translate={-10}
                transition={1.8}
                inertiaScrolling
                data={technologies.map(tech => {
                    return (
                        <CardProgress {...tech} />
                    );
                })}
            />
            <div>
                final
            </div>
        </div>
    );
}

export default App;
